import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    loadShow: true
}

const mutations = {
    changeLoadState: function(state, val){  // 修改 state状态
        state.loadShow = val
    }
}

const actions = {}

export default new Vuex.Store({
    state, mutations, actions
})